body {
  background: black !important;
  display: flex;
  justify-content: center;
  align-items: center;
}
body #login {
  left: 0%;
  width: 50%;
  height: 100vh;
  margin: auto;
  background: #777777;
  position: absolute;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 0px;
}
body #login h1,
body #login form,
body #login #nav,
body #login #backtoblog {
  max-width: 300px;
  margin: auto;
}
@media only screen and (max-width: 600px) {
  body #login {
    width: 100% !important;
  }
}
body a {
  color: white !important;
}

.login {
  height: 100vh;
}
.login h1 {
  background-image: url(/wp-content/themes/rootstrap/inc/images/login-logo.png);
  background-position: center;
  background-repeat: no-repeat;
  height: 120px;
  width: 100%;
}
.login h1 a {
  display: none;
}
.login form {
  background-color: transparent;
  border: none;
}
.login form label {
  color: white;
  font-size: 1rem !important;
  margin-bottom: 20px !important;
}
.login form .user-pass-wrap {
  margin-bottom: 30px;
}
.login form #wp-submit {
  background: #f3f029;
  border-color: #f3f029;
  min-height: 60px;
  font-size: 1rem;
  min-width: 120px;
  color: black;
}
.login form #wp-submit:hover {
  opacity: 0.8;
}
.login #nav {
  font-size: 1rem;
}
.login #nav,
.login #backtoblog {
  text-align: center;
}